body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(45deg, #a8c8f5, #7bb3e0);
  background-size: 400% 400%;
  animation: gradientMove 10s ease infinite;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-gap: 20px;
  padding: 20px;
}
@keyframes gradientMove {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}
header {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px 0;
  grid-column: span 1;
}
.container {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
}
.form-container, .table-container, .lgpd-container {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.form-container {
  grid-column: span 1;
}
.table-container {
  grid-column: span 1;
}
.lgpd-container {
  text-align: center;
}
.add-button, .open-form {
  display: inline-block;
  margin: 20px 0;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}
.add-button:hover, .open-form:hover {
  background-color: #0056b3;
}
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}
th, td {
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
}
th {
  background-color: #007bff;
  color: white;
}
tr:nth-child(even) {
  background-color: #f2f2f2;
}
tr:hover {
  background-color: #ddd;
}
input, select {
  padding: 8px;
  margin: 5px;
  width: 200px;
  border-radius: 5px;
  border: 1px solid #ddd;
}
.save-button, .cancel-button {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s;
}
.save-button {
  background-color: #ff9800;
  color: white;
}
.save-button:hover {
  background-color: #e68900;
}
.cancel-button {
  background-color: #f44336;
  color: white;
}
.cancel-button:hover {
  background-color: #d32f2f;
}
button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}
button:hover {
  background-color: #c82333;
}
td button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}
td button:hover {
  background-color: #0056b3;
}
.modal {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  max-width: 600px;
  width: 100%;
}
.modal-header {
  font-size: 20px;
  margin-bottom: 10px;
}
.modal-body {
  font-size: 16px;
  margin-bottom: 20px;
}
.close-btn {
  background-color: #f44336;
  color: white;
  padding: 10px;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s;
}
.close-btn:hover {
  background-color: #d32f2f;
}
